body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-family: Arial, Helvetica, sans-serif;
  background-color:rgba(80, 80, 120, 0.7);
  margin: 20px;
  
}

.code, code, th {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}
h3 {
  margin:0;
  color: #61dafb;
  padding-top: 10px;
}
/* Responsive layout - makes the two columns/boxes stack on top of each other instead of next to each other, on small screens */
@media (max-width: 600px) {
  #action-menu, #kermitPic {
      width: 100%;
      height: auto;
  }
}

header {  
  background-color: #282c34;
  text-align: center;
  font-size: 35px;
  padding: 0;
}

table {
  width:100%;
}

#kermitPic {
  padding: 10px;
  background-color: #282c34;
  text-align: center;      
}


#action-menu { 
  text-align: center;    
}

footer {
  background-color: rgb(73, 68, 85);
  padding: 10px;
  text-align: center;
  color: white;
}

img {
  margin: 0;
  position: relative;
  top: 5%;
  max-width: 395px;
  max-height: 250px;
  width: 60%;
}

.rules {
  padding:20px;
  background-color: #27262d;
  color: white;
  text-align: left;
}


.playing-mat {
  background-color: #28342a;
  color:antiquewhite;
}

th, table, td {
  margin: 0;
  border: 2px solid wheat;
  border-collapse: collapse;
}

th {
  font-size: large;
}

th, td {
  width:50%;
}

.deals {
  padding: 10px;
}

.cards {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 10px;
}

.pcard {
  margin: 2px;
  font-size: 28px;
  font-weight: bold;
  background: #fff;
  border: 2px solid #000000;
  border-radius: 12%;
  color: black;

  height: 60px;
  line-height: 60px;
  width: 50px;
  text-align: center;
  box-shadow: 1px 1px #042917;
  text-shadow: 1px 1px #a49bd6;
}

.pcard-cover {
  background: #150e55;
}

.actions {
  margin: 15px;
}

.btn-action {
  margin-left: 8px;
  padding: 10px;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color: rgb(236, 235, 221);
  background-color: #463ab8;

  border-radius: 10px;
  border: 2px solid #28342a;

  min-width: 60px;
}

.btn-action:hover {
  cursor: pointer;
  background-color: #352c8a;
  color: rgb(239, 236, 195);
}

.colored {  
  background-clip: text; 
  color: transparent;  
}

.colored-win {
  animation: bg_win 2s ease-in-out infinite;
}

.colored-lost {
  animation: bg_lost 2s ease-in-out infinite;
}

@keyframes bg_win {
  0%    {background-color:#98DC23; left:0px; top:0px;}
  33%   {background-color:#0c7e14; left:200px; top:200px;}
  66%   {background-color:#dcd323; left:200px; top:200px;}
  100%  {background-color:#98DC23; left:0px; top:200px;}  
}

@keyframes bg_lost {
  0%    {background-color:#EE1177; left:0px; top:0px;}
  33%   {background-color:#8811EE; left:200px; top:200px;}
  66%   {background-color:#EE11E6; left:200px; top:200px;}
  100%  {background-color:#EE1177; left:0px; top:200px;}  
}